import { graphql, useStaticQuery } from "gatsby";

type Props = {
  site: {
    siteMetadata: {
      siteTitle: string;
      siteTitleAlt: string;
      siteHeadline: string;
      siteUrl: string;
      siteDescription: string;
      keywords: string;
      siteLanguage: string;
      siteImage: string;
      author: string;
      externalLinks: {
        name: string;
        url: string;
      }[];
      tagsPath: string;
      basePath: string;
      blogPath: string;
      showLineNumbers: boolean;
      projects: {
        name: string;
        description: string;
        url: string;
        stars?: number;
        tags: string[];
      }[];
    };
  };
};

const useSiteMetadata = () => {
  const data = useStaticQuery<Props>(graphql`
    query {
      site {
        siteMetadata {
          siteTitle
          siteTitleEn
          siteTitleAlt
          siteHeadline
          siteUrl
          siteDescription
          keywords
          siteLanguage
          siteImage
          author
          externalLinks {
            name
            url
          }
          tagsPath
          basePath
          blogPath
          showLineNumbers
          projects {
            name
            description
            url
            tags
            stars
          }
        }
      }
    }
  `);

  return data.site.siteMetadata;
};

export default useSiteMetadata;
